<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="atatar_box">
        <img src="../assets/logo.png" alt="">
      </div>

      <!-- 登录表单区域 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- 姓名 -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" placeholder="请输入姓名" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" placeholder="请输入密码" prefix-icon="iconfont icon-3702mima" type="password"></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>

    </div>

  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      // 登录表单的数据绑定对象
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 登录表单规则
      loginFormRules: {
        username: [
          { required: true, message: '请输入用户名称', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 8, message: '长度在 6 到 8 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 重置函数
    resetLoginForm () {
      // this是这个组件的实例对象
      // console.log(this)
      // ref表示j的是表单实例对象，loginFormRef是表单实例对象的名字
      this.$refs.loginFormRef.resetFields()
    },
    // 登录函数
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        // valid是否校验成功
        if (!valid) return
        // 发起post请求，将表单实例对象里面包含的数据当作参数传递出去
        const { data: res } = await this.$http.post('login', this.loginForm)
        // console.log(res)
        if (res.meta.status !== 200) return this.$message.error('登录失败！')
        this.$message.success('登录成功！')

        // 1. 登录成功后，将token保存到sessionStorage中
        window.sessionStorage.setItem('token', res.data.token)
        // 2. 通过编程式路由导航，跳转到Home主页中
        this.$router.push('/home')
        // 3. 在路由配置文件中，添加路由规则
      })
    }
  }
}
</script>

<style lang='less' scoped>
.login_container{
  background-color: #2b4b6b;
  height: 100%;
}
.login_box{
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position:absolute;
  left: 50%;
  top:50%;
  transform: translate(-50%,-50%);

  .atatar_box{
    height: 130px;
    width: 130px;
    border:1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #fff;
    box-shadow: 0 0 10px #ddd;

    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form{
  width: 100%;
  box-sizing: border-box;
  position:absolute;
  bottom:0;
  padding:0 20px;
}
.btns{
  display: flex;
  // 放在右下角
  justify-content: flex-end;
}
</style>
